@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Khmer:wght@100;500&family=Poppins:wght@200;500;600;700;800&display=swap');
:root{
  --violet-blue:#324AB2;
  --tufts-blue:#3E8EDE;
  --gray:#f3f3f3;
  --white:#fff;
  --black:#000;
  --light-black:#717171;
  --transition:all 320ms ease-in-out;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 1.6rem;
  line-height: 1.7;
  color: var(--black);
}
body{
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
  margin: 0 1.2rem;
  position: relative;
}


/* default */
a{
  text-decoration: none;
  color: var(--light-black);
}
ul{
  list-style: none;
}

button{
  outline: 0;
  border: none;
  background-color: transparent;
  cursor:pointer;
}
img{
  width: 100%;
  object-fit: cover;
  display: block;
}

input{
  border: none;
  outline: none;
  font-size: 1.6rem;
}

.resize-animation-stopper *{
  animation: none !important;
  transition: none !important;
}


::-webkit-scrollbar{
  border-radius: 0.5rem;
  width: 10px;
}
::-webkit-scrollbar-thumb{
  background-color:rgba(0, 0, 0, 0.278);
  border-radius: 0.5rem;
}
