.container{
    display: flex;
    flex-direction:column ;
    gap: 50px;
}
.section-title{
    text-align: start;
    padding: 10px 15px;
    margin: 10px 0;
    border-radius: 0.6rem;
    background-color: #9694942c;
}
.booklist-content{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 20px;
}
.book-item{
    width: 330px;
    height: auto;
    padding: 10px;
    overflow: hidden;    
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
.book-item-img{
    height: 70%;
}
.book-item-info{
    height: 30%;
}
.img_cover{
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

.book-item-info-title{
    text-align: center;
    height:40%;
    padding: 20px 0;
    text-overflow: ellipsis;
    overflow: hidden;
}
.title{
    font-weight: bold;
    white-space: nowrap;
    font-size: 1rem;
    color: #000;
    
}
.book-item-author,
.book-item-publish,
.book-item-edition{
    display: flex;
    height:15%;
    gap: 10px;
    font-size: 0.7rem;
    padding: 0 15px;
}

.author{
    font-weight: bold;
}
.author-des{
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}




