.conatiner{
    height: 120px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.navbar{
    position: relative;
}
.toggler{
    width: 100%;
    justify-content: space-between;
}
.toggler,
.navbar-brand{
    display:flex
}

.name-logo{
    padding: 15px 18px;
    color: var(--black);
    font-size: 1rem;
    font-weight: 600;
}
img{
    width: 70px;
    height: 70px;
    border-radius: 1.2rem;
    object-fit: cover;
}
.toggler-button{
   
    display: none;
}

.navbar-nav{
    display: flex;
}
.nav-items{
    padding: 0 0.7rem;
}
.nav-items:hover .nav-link{
    color:var(--black);
    transition: .3s !important;
}
.close-button{
    display: none;
}
.close-icon{
    display: none;
}
@media screen and (max-width:992px){
    .navbar-collape{
        display: none;
    }
    .toggler-button{
        display: block;
        transition: .4 !important;
    }
    .show-navbar-collape{
        display: block;
        position: fixed;
        right:0;
        top:0;
        width: 300px;
        height:100%;
        background-color: #000;
        color: #fff;
        z-index: 999;
        overflow-y:none;
        padding: 1.2rem 0;
        transition: .5s ease-in-out !important;
    }
    .navbar-nav{
        flex-direction: column;
        gap: 0.6rem;
        text-align: center;
        position: relative;
    }
    .close-icon{
        display: block;
        position: absolute;
        top: -1rem;
        right: 5px;
    }
    .nav-items{
        height: calc(100%/2);
    }
    
}