.book-detail{
    overflow: hidden;
    text-overflow: ellipsis;
}
.back-btn{
    text-align: start;
    padding: 15px 10px;
    display: flex;
    background-color:black;
    color: #fff;
    border: none;
    outline-offset: none ;
    transition: .4s ease-out;
    border-radius: 1rem;
    width: 150px;
    margin: 5px 0;
    gap: 10px 15px;
}
.back-btn:hover {
    background-color: rgba(10, 10, 10, 0.381);
    box-shadow: 5px 2px 8px black;
    transition: .4s ease-in;
    color: black;
}
.back-btn:hover:active {
    border: 1.5px solid #000;
}
.back-btn .go-back{
    font-size: 0.7rem;
    font-weight: bold;
}

.book-detail-content{
    display: flex;
    flex-direction: row-reverse;
    grid-template-columns:repeat(1fr,20px);
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 10px;
}
.detail-img{
    width:25%;
    height: 400px;
    & .dt_image{
        width: 100%;
        height: 100%;
        border-radius:0;
    }
}
.book-info{
    display: flex;
    flex-direction: column;
    max-width: 70%;
    gap: 20px;
}
.titles{
    font-size: 1.5rem;
    max-width: 350px;
    font-weight: bold;
    text-overflow: ellipsis;
}
.description,
.text-italic{
    max-width: 350px;
    color: #6e6e6e;
    font-size: 0.8rem;
    text-transform: capitalize;
    text-overflow: ellipsis;
    line-height: 1.2;
}
.place,
.time,
.subject{
    font-weight: bold;
    margin-right: 10px;
}

@media screen and (max-width:772px){
    .book-detail-content{
        flex-direction: column;
        gap: 25px;
    }
    .detail-img{
        width: 100%;
    }
    .book-info{
        max-width: max-content;
    }
    .book-info-item{
        min-height: 100px;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}
@media screen and (max-width:1150px){
    .book-detail-content{
        flex-direction: column;
        align-items: center;
        gap: 25px;
    }
    .detail-img{
       width:400px;
    }
}