.search-form-content{
    width: 600px;
    padding: 5px 10px;
}
.search-from{
    width: 100%;
    height: 60px;
    position: relative;
}
.form-control{
    padding: 7px 10px;
    width: 100%;
    height: 100%;
    border-bottom-left-radius: 14px;
    border-top-right-radius: 14px;
    font-size: 1.1rem;
    transition: .4s ease-in-out inherit;
}
.form-control:focus{
    transition: .4s ease-in-out inherit;
    border: 2px solid #000;
}
.form-control:focus::placeholder{
    transform: translateY(-10px);
    transition: .5s ease-in !important;
    font-size: 0.5rem;
    font-weight: 600;
    padding: 5px 0;
}

.form-control::placeholder{
    font-size: 1.1rem;
    position: absolute;
    transition: .5s ease-in !important;

}
.button-input{
    position: absolute;
    top: 0.5rem;
    right: 5px;
}
.icon-search{
    font-weight: 600;
}

@media screen and (max-width:772px){
    .search-form-content{
        width: 300px;
        padding: 5px 10px;
    }
    .form-control{
        font-size: 0.7rem;
    }
    .form-control::placeholder{
        font-size: 0.7rem;
    }
    .icon-search{
        font-weight: 600;
        font-size: 0.8rem;
    }
    
}