.header-content{
    width: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 75vh;
    color:var(--white);
    background:linear-gradient(rgba(0, 0, 0, 0.505),rgba(255, 255, 255, 0.138)),url("../../pictures/slide_2.jpg" ) center/cover no-repeat ;
    flex-direction: column;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.header-title{
    text-align: center;
    text-transform: capitalize;
    font-size: 2.1rem;
}
.header-des{
    text-align: center;
    padding: 0 0.5rem;
    max-width: 1000px;
}


@media screen and (max-width:992px){
    .header-title{
        font-size: 1.5rem;
    }
    .header-des{
        font-size: 0.8rem;
    }
}
