.about-content{
    display: flex;
    flex-direction: row-reverse;
    gap: 25px;
    justify-content: space-between;
    margin-bottom: 15px;
}
.about-img{
    max-width: 35%;
    animation: move 1s infinite ease-in-out;
}
.a-img{
    width:100%;
    height: 65%;
    border-radius: 10px;
    box-shadow: 10px 10px 2px rgba(108, 110, 110, 0.192);
    
}
.about-info{
    max-width: 80%;
}
@media screen and (max-width:1350px) {
    .a-img{
        height: 50%;
    }
}
@media screen and (max-width:1100px){
    .about-content{
        flex-direction: column;
        align-items: center;
    }
    .about-img{
        max-width: 40%;
    }
}
@media screen and (max-width:772px){
    .about-img{
        max-width: 80%;
        height: 70%;
    }
}

@keyframes move{
    0%{
        transform: translate(-4px, -5px);
    }
    100%{
        transform: translate(2px, 2px);
    };
}